.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #000;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  background: rgba(252, 154, 27, .95) radial-gradient(circle at 50% 0, rgba(252, 154, 27, .95), rgba(224, 92, 10, .35));
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 8px 12px;
  box-shadow: inset 0 -3px 8px rgba(252, 154, 27, .65), inset 0 -3px 8px rgba(252, 255, 255, .3), inset 0 3px 8px rgba(252, 255, 255, .3), 0 5px 5px -5px rgba(0, 0, 0, .5);
}

.btnShadow {
  border-radius: 10px;
  background: rgba(233, 92, 5, .95) radial-gradient(circle at 50% 0, rgba(252, 136, 27, .95), rgba(224, 92, 10, .35)) !important;
}

input, textarea {
  font: inherit;
  border: none;
  padding: 8px;
}

textarea {
  resize: none;
}

.shadow {
  border: .6px solid rgba(154, 147, 140, .4);
  border-radius: 5px;
  box-shadow: inset 1px 1px 6px rgba(154, 147, 140, .3), 1px 1px 5px #fff, inset -1px -1px 6px rgba(154, 147, 140, .3), 1px 1px 5px #fff;
}

